import { makeStyles } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import 'react-toastify/dist/ReactToastify.css';
import React from 'react';
import userService from '../../services/UserService';
import { toast } from 'react-toastify';


const useStyles = makeStyles((theme)=>({

    container:{
        display:"flex",
        justifyContent:"center",
        alignItems:"center",
        height:"300px"
    },
    child:{
        width:"400px",
        
    },
    margin:{
        marginTop:"15px"
    }

}));
const Login = () => {
    const [email,setEmail] = React.useState("user1@gmail.com");
    const [password,setPassword] = React.useState("12345");
    const classes = useStyles();
    return ( <div className={classes.container}>
        <div className={classes.child}>
            <TextField label="email" fullWidth value={email} onChange={e=>{
                setEmail(e.target.value);
            }}/> <br />
            <TextField label="password" type="password" fullWidth value={password} onChange={e=>{
                setPassword(e.target.value);
            }}/> <br />
            <Button variant="contained" className={classes.margin} color="primary" onClick={e=>{
                userService.login(email,password).then(data=>{
                    console.log(data);
                    window.location.href = "/";
                }).catch(err=>{
                    console.log(err);
                    toast.error(err.response.data, {
                        position: toast.POSITION.TOP_RIGHT
                      });
                })
            }}>Login</Button>
        </div>

    </div> );
}
 
export default Login;