import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { useCookies } from 'react-cookie';

const useStyles = makeStyles({
  main: {
    minWidth: 650,
    margin:'10px',
    display:'grid'
  },
  table:{
      maxWidth:300,
  }
});





export default function CartItem({cartItem}) {
  const classes = useStyles();
  const [cookies, setCookie] = useCookies(['cart']);
  const [tprice,setTotalPrice] = React.useState(0);
  console.log(cartItem);
  return (
    <div className={classes.table}><TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell align="right">Price</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {cartItem.map((p,index) => (
            <TableRow key={index}>
              <TableCell component="th" scope="row">
                {p.name}
              </TableCell>
              <TableCell align="right">{p.price}</TableCell>
            </TableRow>
          ))}
        <TableRow style={{borderTop:"2px solid grey"}}><TableCell style={{fontWeight:"bolder"}} component="th" scope="row">Total</TableCell>
          <TableCell style={{fontWeight:"bolder"}} align="right">30200</TableCell></TableRow>
        </TableBody>
      </Table>
    </TableContainer>
    </div>
  );
}