import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import img from '../../src/payment.png'
import img2 from '../../src/service2.png'
import img3 from '../../src/service3.png'

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor:'white'
  },
}));

export default function Features() {
  const classes = useStyles();

  return (
    <div className={classes.root}>

      <h1 style={{color:'white'}} >Services</h1>
    <ul class="cards">
  <li>
    <a href="" class="card">
      <img src={img} class="card__image" alt="" />
      <div class="card__overlay">
        <div class="card__header">
          <svg class="card__arc" xmlns="http://www.w3.org/2000/svg"><path /></svg>                     
          <div class="card__header-text">
            <h3 class="card__title">Online Payment</h3>            
            <span class="card__status"></span>
          </div>
        </div>
        <p class="card__description">Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores, blanditiis?</p>
      </div>
    </a>      
  </li>

  <li>
    <a href="" class="card">
      <img src={img2} class="card__image" alt="" />
      <div class="card__overlay">        
        <div class="card__header">
          <svg class="card__arc" xmlns="http://www.w3.org/2000/svg"><path /></svg>                 
          <div class="card__header-text">
            <h3 class="card__title">Delievery On Time</h3>
            <span class="card__status"></span>
          </div>
        </div>
        <p class="card__description">Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores, blanditiis?</p>
      </div>
    </a>
  </li>
  
  
  <li>
    <a href="" class="card">
      <img src={img3} class="card__image" alt="" />
      <div class="card__overlay">
        <div class="card__header">
          <svg class="card__arc" xmlns="http://www.w3.org/2000/svg"><path /></svg>                 
          <div class="card__header-text">
            <h3 class="card__title">Free Home Delivery</h3>
            <span class="card__status"></span>
          </div>          
        </div>
        <p class="card__description">Lorem ipsum dolor sit amet consectetur adipisicing elit. Asperiores, blanditiis?</p>
      </div>
    </a>
  </li>    
</ul>
    </div>
  );
}