import { Grid, makeStyles } from '@material-ui/core';
import React from 'react';

import DeleteForeverTwoToneIcon from '@material-ui/icons/DeleteForeverTwoTone';
import productService from '../../services/ProductService';
import UpdateForm from '../UpdateForm';
import userService from '../../services/UserService';
import img from '../../../src/pizza.png';
import { useCookies } from 'react-cookie';
const useStyles = makeStyles((theme)=>(
    {
      icon: {
          position:"relative",
          bottom:theme.spacing(1),
          marginLeft:"3px",
          cursor:"pointer",
          
      },
      left:{
          direction:"rtl",
      },
      inline:{
          display:"inline",
      },
      
        
    }));
const SingleProduct = ({product,onDelete}) => {
  const [cookies, setCookie] = useCookies(['cart']);
    const classes = useStyles();

    React.useEffect(()=>{
      if(!cookies.cart){
        setCookie("cart",JSON.stringify([]));
      }
    })
    const addProductToCart = (product) => {
      console.log(product);
      let newCart = [...cookies.cart];
      newCart.push(product);
      setCookie("cart",JSON.stringify(newCart));
    }
    return ( <Grid item xs={3}>
        
    <div className="cards">
    <div className="container page-wrapper">
      <div className="page-inner">
        <div className="row">
          <div className="el-wrapper deco">
            <div className="box-up">
              <img className="img" src={img} alt="abc" />
              <div className="img-info">
                <div className="info-inner">
                  <span className="p-name">{product.name}</span>
                  <span className="p-company">Yeezy</span>
                </div>
                <div className="a-size">Available sizes : <span class="size">S , M , L , XL</span></div>
              </div>
            </div>
    
            <div className="box-down">
              <div className="h-bg">
                <div class="h-bg-inner"></div>
              </div>
    
              <a className="cart" style={{cursor:'pointer'}}onClick={e=>{addProductToCart(product)}}>
                <span className="price">${product.price}</span>
                <span className="add-to-cart">
                  <span className="txt">Add to cart</span>
                </span>
              </a>
            </div>
          </div>
          </div>
          </div>
          </div>
          </div>
        {userService.isAdmin() && <>
        <div className={classes.left}>
        <DeleteForeverTwoToneIcon className={classes.icon} fontSize="large" htmlColor="#DD0300" onClick={e=>{
            productService.deleteProduct(product._id).then(data =>{
                console.log(data);
                onDelete();
            }).catch(err =>{
                console.log(err);
            })
        }}/>
        <UpdateForm product={product} onDelete={onDelete}/>
        </div></>}
        <hr />
        </Grid> );
}
 
export default SingleProduct;