import { makeStyles } from '@material-ui/core';
import React, { useState } from 'react';
import { useEffect } from 'react';
import { useCookies } from 'react-cookie';
import CartItem from './CartItem';
const useStyles = makeStyles((theme) =>({

    root:{

        display:'flex',
        justifyContent:'center',
    },
    im:{
        // width:'auto',
        height:'50vh'
    },
    head:{
        display:'flex',
        justifyContent:'center'
    },
    color:{
        color:'grey',
        fontSize:'2rem',

    }
}))

const Cart = () => {
    const [val,setVal] = React.useState(0);
    const [cookies, setCookie] = useCookies(['cart']);
    const [cartProducts,setCartProducts] = useState([]);
    React.useEffect(()=>{
        if(cookies.cart){
            setCartProducts(cookies.cart);
        }
    },[cookies])
    const classes = useStyles();

    console.log(cookies);
   let url = `${process.env.PUBLIC_URL + "/images/empty.png"}`;
    return ( <>{cookies.cart.length ?(<div style={{margin:'auto'}}><h2 className={classes.color}>Cart Items</h2><CartItem cartItem={cartProducts}/></div>):(<>
    <h2 className={classes.head,classes.color}>Cart is empty </h2>
    <div className={classes.root}>
     
    <img className={classes.im} src={url} alt="img" />
    </div></>)}
   </> );
}
 
export default Cart;