import { makeStyles } from '@material-ui/core';
import Typewriter from 'typewriter-effect';
import React from 'react';
import { Button } from '@material-ui/core'; 
import img from '../../src/back.png'
const useStyles = makeStyles((theme) => ({
    main:{
        backgroundColor:'white',
        height:'90vh',
        width:'100%',
        display:'flex',
        flexDirection:'row',
        marginTop: '5px',
        justifyContent:'center',
       
    },
    left:{
        width:'50%',
        display:'inline-bliock',
        alignItems:'center',
        margin:' 10% auto',
        justifyContent:'center',
        paddingLeft:"4%",
        paddingRight:'4%'
    },
    right:{
        width:'50%',
        display:'inline-block',
        alignItems:'center',
        justifyContent:'center',
        borderTopLeftRadius:"40px",
        borderBottomLeftRadius:"40px",
    },
    imgsize:{
        width:'100%',
        height:'auto',
        padding:'10px'
    },
    btn:{
        border:' 1px solid grey',
        color:'grey',
        '&:hover':{
            backgroundColor:'white',
            border:' 1px solid #FE5F1E',
            color: '#FE5F1E'

        }
    }
    
}));
const Demotxt = () => {
    const classes = useStyles();
    const str1 = "<h3>The True Face Of <span style={{color:'#FE5F1E'}}>Junk Food</span></h3>";
    const str3 = "<h3>Be The Fastest In Delivering Your Food</h3>";
    const str4 = "<h3>Order Healthy And Fresh Food Any Time</h3>";
    return ( <div className={classes.main}>
        <div className={classes.left}>
            <div style={{height:'40%'}}><span style={{color:'white'}}>l</span><Typewriter options={{loop:true,cursor:""}} onInit={(typewriter)=>{
                typewriter.typeString(str1).start().pauseFor(500).deleteAll().typeString(str3).start().pauseFor(500).deleteAll().typeString(str4).start().pauseFor(500).deleteAll();;
            }} /></div>
            <br />
            
            <a href="#menu"><div><Button  className={classes.btn}>Order Now</Button></div></a>
        </div>
        <div className={classes.right}>
            <img className={classes.imgsize} src={img} alt="" />
        </div>
    </div> );
}
 
export default Demotxt;