import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import banner1 from '../../../src/banner1.jpg';
import banner2 from '../../../src/banner2.jpg';
import banner3 from '../../../src/banner3.jpg';
import banner4 from '../../../src/banner4.jpg';
import { classes } from "istanbul-lib-coverage";
import { classExpression } from "@babel/types";

export default function Slide() {
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    autoplay: true
  };
  return (
    <div style={{margin:'2%'}}>
    <Slider {...settings}>
      <div className="ban-center">
        <img className="banner" src={banner1} alt="" />
      </div>
      <div className="ban-center">
        <img className="banner" src={banner2} alt="" />
      </div>
      <div className="ban-center">
        <img className="banner" src={banner3} alt="" />
      </div>
      <div className="ban-center">
        <img className="banner" src={banner4} alt="" />
      </div>
    </Slider>
    </div>
  );
}