
import React from 'react';
import { makeStyles } from '@material-ui/styles';
import Slide from './landingpagecomp/Slide';
import Features from './Features';
import ItemTab from './ItemTab';
import Demotxt from './demotxt';
const useStyles = makeStyles((theme)=>({
    root:{
        minHeight:'100vh',
        backgroundImage:`url(${process.env.PUBLIC_URL + "/images/background.png"})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
    },
    hero:{
        width:'100%'
    },
    container:{
        display:'flex',
        justifyContent:'between',
        alignItems:'center'
    },
    h:{
        height:'2000px'
    },
    max:{
        maxWidth:'70%',
        maxHeight:'300px',
        margin:'auto'
    },
    center:{
        width:'100%',
        margin: 'opx auto',
        textAlign:'center',
        color:'grey',
        marginBottom:'10px'
        
    }
}))

export default function LandingPage(){
    const classes = useStyles();
    return (<>
    <div className={classes.hero}>
    <div className={classes.container}>
        <Demotxt />
    </div>
    </div>
    
    <div>
    <h1 className={classes.center}>Services</h1>
        <div>
        <Features />
        </div>
        <h1 className={classes.center}>New Offers</h1>
        <br />
        <div className={classes.max} >
        <Slide />
        </div>
        <br />
        <div className={classes.center}><h1 id="menu">Menu</h1></div>
        <br />
        <div className={classes.h}>
        <ItemTab />
        </div>
        

        

    
    </div>
    </>)
};