import Products from "../components/products/products";
import GenericService from "./GenericService";
import jwt_decode from "jwt-decode";
import { ReplyTwoTone } from "@material-ui/icons";
class UserService extends GenericService{
    constructor(){
        super();
    }
    login = (email,password) => new Promise((resolve,reject)=>{
        this.post("users/login",{email,password}).then(token=>{
            localStorage.setItem("token",token);
            resolve(token);
        }).catch(err=>{
            reject(err);
        })
    });
    register = (name,email,password) => new Promise((resolve,reject)=>{
        this.post("users/register",{name,email,password}).then((data)=>{
            resolve(data);
        }).catch((err)=>{
            reject(err);
        })
    })
    logout = ()=>{

        localStorage.removeItem("token");
    };
    isLoggedIn= ()=>{
        return localStorage.getItem("token") ? true : false;
    }
    getLoggedInUser = ()=>{
        try{
            const jwt = localStorage.getItem("token");
            return jwt_decode(jwt);
        }catch(err){
            return null;
        }

    }
    isAdmin=()=>{
        if(this.isLoggedIn()){
            if(this.getLoggedInUser().role == "admin") return true; else return false;
        }
        else return false;
    }
}
let userService  = new UserService;
export default userService;